@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.movies{display:flex;position:relative;padding:0 4rem 2rem;background:#eee;flex-direction:row;align-items:flex-start;align-content:flex-start;flex-wrap:wrap}
.movies .movie{position:relative;width:48%;min-height:20rem;margin:4rem 0 4rem 4%;padding-top:2rem;background:#fff;flex-grow:1;box-shadow:0 0.5rem 1rem #aaa;-webkit-transition:all .3s;transition:all .3s}
.movies .movie:nth-child(odd){margin-left:0}
.movies .movie > img{position:absolute;top:-1rem;left:2rem;width:10rem;height:auto;box-shadow:0 1rem 2rem #ccc;z-index:1;-webkit-transition:all .3s;transition:all .3s}
.movies .movie > .rating{position:absolute;top:15rem;left:2rem;width:10rem;font-size:1.2rem;text-align:center;color:#aaa;-webkit-transition:all .3s;transition:all .3s}

.movies .movie .movie_data{padding:0 2rem 2rem 14rem}
.movies .movie .movie_data .moive_title{font-size:2rem}
.movies .movie .movie_data .movie_year{padding-left:1rem;font-size:1.2rem}
.movies .movie .movie_data .movie_genres{display:block}
.movies .movie .movie_data .genres_list{display:flex;all:unset}
.movies .movie .movie_data .genres_list li{display:inline-block;margin:0.5rem 1rem 0 0;padding:0.1rem 0.2rem;border:1px solid #ddd;list-style:none;}
.movies .movie .movie_data .movie_summary{margin-top:1rem;font-size:1.5em}
.movies .bt_like{all:unset;cursor:pointer}
.movies .bt_like > i{position:absolute;bottom:1.5rem;right:1.5rem;color:#ddd;-webkit-transition:all .3s;transition:all .3s}
.movies .bt_like > i.fill{color:#ff3066 }
.movies .movie:hover{background:#ffffe2}
.movies .movie:hover > img{top:2rem}

/* tablet(low quality), mobile(landscape) */
@media (min-width: 481px) and (max-width: 767px) {
.movies .movie{width:100%;margin:2rem 0}
.movies .movie:first-child{margin-top:4rem}
}

/* mobile(portrait) */
@media (min-width: 320px) and (max-width: 480px) { 
.movies .movie{width:100%;min-height:auto;margin:2rem 0}
.movies .movie:first-child{margin-top:4rem}
.movies .movie > img{position:relative;top:0;left:50%;margin-left:-5rem}
.movies .movie > .rating{display:block;position:relative;top:1rem;left:50%;width:10rem;margin-left:-5rem}
.movies .movie .movie_data{padding:2rem;text-align:center}
.movies .movie:hover > img{top:2.5rem}
}
html{font-size:10px;font-family:'Open Sans', sans-serif}
body{background:#eee}
.container{width:98%;margin:0 auto;border-style:solid;border-width:0.5vw;-webkit-border-image: -webkit-gradient(linear, left top, right top, from(#ff3066), to(#7c4dff));-webkit-border-image: linear-gradient(to right, #ff3066 0%, #7c4dff 100%);border-image: -webkit-gradient(linear, left top, right top, from(#ff3066), to(#7c4dff));border-image: linear-gradient(to right, #ff3066 0%, #7c4dff 100%);border-image-slice:1;background:#f7f7f7;box-sizing:border-box}
.loader{display:table-cell;width:100vw;height:92vh;text-align:center;vertical-align:middle}
.loader_txt{display:inline-block;font-size:3rem;font-weight:bold;color:transparent;-webkit-animation: loading 1s infinite;animation: loading 1s infinite}

@-webkit-keyframes loading {
    0% {
        background:-webkit-gradient(linear, right top, left top, from(#ff3066), to(#7c4dff));
        background:linear-gradient(-90deg, #ff3066, #7c4dff);
        -webkit-background-clip:text;
        -webkit-text-fill-color:transparent;
        -moz-background-clip:text;
        -moz-text-fill-color:transparent;
        -o-background-clip:text;
        -o-text-fill-color:transparent;
    }
    50%{
        background:-webkit-gradient(linear, left bottom, left top, from(#ff3066), to(#7c4dff));
        background:linear-gradient(0deg, #ff3066, #7c4dff);
        -webkit-background-clip:text;
        -webkit-text-fill-color:transparent;
        -moz-background-clip:text;
        -moz-text-fill-color:transparent;
        -o-background-clip:text;
        -o-text-fill-color:transparent}
    100% {
        background:-webkit-gradient(linear, left top, right top, from(#ff3066), to(#7c4dff));
        background:linear-gradient(90deg, #ff3066, #7c4dff);
        -webkit-background-clip:text;
        -webkit-text-fill-color:transparent;
        -moz-background-clip:text;
        -moz-text-fill-color:transparent;
        -o-background-clip:text;
        -o-text-fill-color:transparent
    }
}

